*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  box-sizing: border-box;
  padding-top: 3rem; }

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7; }

.navigation {
  display: grid;
  align-items: center;
  grid-template-columns: 10rem repeat(4, 1fr) 10rem; }
  .navigation__logo-box {
    grid-column: 2/3; }
  .navigation__logo {
    height: 5rem; }
  .navigation .menu {
    grid-column: 3/6;
    justify-self: end;
    display: flex;
    text-align: center; }
    .navigation .menu__checkbox {
      display: none; }
    .navigation .menu__list {
      display: flex;
      list-style: none;
      text-align: center;
      width: 100%; }
    .navigation .menu__item {
      margin: 1rem; }
    .navigation .menu__link:link, .navigation .menu__link:visited {
      display: inline-block;
      font-weight: 300;
      padding: 1rem 2rem;
      text-decoration: none; }
      .navigation .menu__link:link span, .navigation .menu__link:visited span {
        display: none; }

.header {
  display: grid;
  grid-template-columns: 10rem repeat(2, 1fr) 10rem; }
  .header__hero {
    grid-column: 2/3; }
  .header__img {
    position: absolute;
    right: 0;
    top: 3rem;
    z-index: -1;
    height: 50rem; }
  .header__img-box {
    grid-column: 3/4; }
